@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body{
  overflow-y: scroll;
  font-family: 'Roboto', sans-serif;
}

h1, h2 {
  margin: 0.1rem 0.5rem;
}

h3 {
  margin: 0.5rem 0.5rem;
}

.App {
  margin: auto;
  overflow: auto;
  min-height: 300px;
  max-width: 500px;
  padding: 30px;
}

.link-button {
  width: min-content;
  display: flex;
  align-items: center;
}

a {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}

.card{
  box-shadow:  0 0px 8px 0 rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  padding: 0.5rem;
  background-color: white;
}

header {
  margin: 1.5rem 0rem;
  width: 100%;
}

button {
  display: block;
  color: white;
  padding: 1rem;
  margin: 0.5rem;
  border:0px;
  border-radius: 5px;
  text-transform: capitalize;
}

button:hover {
  color: #DDDDDD;
}

button:focus {
  outline: none;
}

button:active {
  transform: scale(0.995);
}

.wide-btn {
  display: flex;
  justify-content: space-between;
}

.add-box {
  margin: 0.25rem 0rem;
}

.draggable {
  padding: 0.25rem 0rem;
}

.box-wrapper {
  margin: 0rem 0.5rem;
}

.color-box-wrapper {
  margin: 0.5rem 0.5rem;
}


.color-box {
  min-height: 3.5rem;
  position: relative;
}

.color-box-text{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-weight: bold;
  color: #999;
}

.color-header{
  display: flex;
  justify-content: space-between;
}

.color-box-icons{
  width: 50%;
  padding: 0rem 0.75rem;
  display:flex;
  position: relative;
}

.left-icons{
  justify-content: flex-start;
}

.right-icons{
  justify-content: flex-end;
}

.button-icon-btn {
  height: 1.25rem;
  width: 1.25rem;
}

.icon {
  color: #999;
  width: fit-content;
  margin: 0.25rem 0.5rem;
}

.icon:hover {
  color: #CCC;
}

.small-icon-btn {
  height: 1rem;
  width: 1rem;
}

.btn {
  cursor: pointer;
}


.delete-icon:hover {
  color: red;
}

header input {
  padding: 0.5rem;
  width: 100%;
  border: none;
  font-size: inherit;
  font-family: 'Roboto', sans-serif;
  box-sizing:border-box
}

header input:hover {
  background-color: #EEEEEE;
  border: none;
  font-family: 'Roboto', sans-serif;
}

.eader input:focus {
  background-color: #EEEEEE;
  border: none;
  font-family: 'Roboto', sans-serif;
}

.no-colors{
  color:grey;
  padding: 1rem;
}